import { Component } from '@angular/core';
import { AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { Socket } from 'ng-socket-io';

@IonicPage()
@Component({
  selector: 'page-chat-home',
  templateUrl: 'chat-home.html',
})
export class ChatHomePage {

  
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  v1:any;
  pofilel: string = './assets/img/profilelocal.png';
  v2:any;
  v3:any;
  v4:any;
  sidist:boolean=false;
  tiposus: any=[];
 
  idsuscriptor: any;
  fotott: any;
  nombrett: any;
  username: string = "";
  nidsuscriptor: any;
  idempresa:any;
  idafiliado:any='na';
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  toUser : {toUserId: string, toUserName: string};


  constructor( private socket: Socket,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })

      this.toUser = {
        toUserId:'210000198410281948',
        toUserName:'Hancock'
      }

  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  
      

  pideregistros() { 
      let self = this;
      self.loading = true;

      
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
    this.idsuscriptor=this.tiposus[3]
    this.nombrett=this.tiposus[4]
    this.fotott=this.tiposus[9]+","+this.tiposus[10]
  if(this.tiposus[0]=='Distribuidor')
  {
    this.sidist=true;
  }
  else
  {
    this.sidist=false;
  }    


  this.regService.getReg( 'chatunouno/' +  this.idsuscriptor,'usermsgs').then((data) => {
 
    this.items=data;
    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});


});


     
  }
  ionViewDidLoad() {
    

    //this.idsuscriptor=this.navParams.data.record.nodpi;
    //this.nidsuscriptor=this.navParams.data.record.nombre +' '+ this.navParams.data.record.apellido;

    this.pideregistros();

    this.translateService.get(["CANCEL",
    "INGRESAR",
    "BUSCARPOR",
    "BUSCAR"
  ]).subscribe(
    (values) => {
     this.v1=values.CANCEL
     this.v2=values.INGRESAR
     this.v3=values.BUSCARPOR
     this.v4=values.BUSCAR
    });

  }

  ngOnInit() {
    let self = this;
    self.loading = true;
    this.storage.get('username').then((value) => {
      this.username = value;
    
  }); 

  

  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  
  chatt(item : any, no) : void
  {
    this.socket.connect();
    this.socket.emit('set-nickname', item._id);
    this.navCtrl.push('ChatPage', { record :{  toUserId:this.idsuscriptor,
      toUserName:this.nombrett,avatar:this.fotott} ,grupal:'no',
       
    
      record2:{  toUserId:item._id,
    toUserName:item.nombre,avatar:item.foto}});
    
  }
  
  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
          let recordID 		: string		=  this.username;
          this.regService.deleteReg(item._id + '/'+ recordID+ '/' + this.idempresa + '/' + this.idafiliado,'userchats').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
        
      
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ChatHomecreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('ChatHomecreatePage',{ record : null },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
  // this.items.concat([{idcomprasaldo:data.idcomprasaldo,idsuscriptor:data.idsuscriptor,fecha:data.fecha,monto:data.monto,nombre:data.noplaca}]);
      }  
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }

   excludeTracks: any = [];

   presentFilter() {
    
  }

  

}



